@import url(https://fonts.googleapis.com/css2?family=Advent+Pro:wght@300;400;500;600;700&family=Neucha&family=Quicksand:wght@300;400;500;600;700&display=swap);
*{padding:0;margin:0;list-style:none;outline:none;box-sizing:border-box;font-family:'Neucha', cursive}body{background:#E5E5E5}.todo{max-width:750px;margin:220px auto;display:flex}.todo__sidebar{background:#e8ded5;width:200px}.todo__tasks{background:#FFFFE0;width:550px;padding:55px;background:url(/react-todo-app/static/media/oHPNYn1wymI.d8048530.jpg) 0 0/cover no-repeat}.field{padding:10px;background:#FFFFFF;border:1px solid #EFEFEF;box-sizing:border-box;border-radius:4px}.field:focus{border-color:#767676}

.list{margin:60px 30px 0px}.list__header{display:flex;align-items:center;margin-bottom:25px;cursor:pointer;-webkit-filter:drop-shadow(0px 4px 4px rgba(0,0,0,0.25));filter:drop-shadow(0px 4px 4px rgba(0,0,0,0.25))}.list__header p{font-size:24px;line-height:16px;letter-spacing:0.2px;margin-left:5px}.list__body a{text-decoration:none;color:black}.list__body .listItem{padding:11px 10px;display:flex;align-items:center;justify-content:space-between}.list__body .listItem svg{stroke:#ccb59b}.list__body .listItem svg:hover{stroke:black}.list__body .listItem .listItemWrap{display:flex;align-items:center}.list__body p{font-size:14px;line-height:16px;cursor:pointer;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.list__body .listHeader p{font-size:26px;line-height:22px;letter-spacing:0.2px;-webkit-filter:drop-shadow(0px 4px 4px rgba(0,0,0,0.25));filter:drop-shadow(0px 4px 4px rgba(0,0,0,0.25))}.list__body .listHeader svg{display:none}.list__footer{margin:27px 0px}.list__footer button{display:flex;align-items:center;border:none;background-color:transparent;cursor:pointer}.list__footer button:hover{opacity:0.8}.list__footer button p{font-size:14px;line-height:17px;color:#767676;margin-left:5px}.active{background:#FFFFFF;box-shadow:0px 2px 10px rgba(0,0,0,0.05);border-radius:4px}.circle{width:10px;height:10px;border-radius:50%;margin-right:10px}.green{background-color:#42B883}.blue{background-color:#64C4ED}.orange{background-color:#FF4500}.red{background-color:#FF0000}.indigo{background-color:#4B0082}

.addListPopup{position:absolute;max-width:235px;background:#FFFFFF;box-shadow:0px 6px 10px rgba(0,0,0,0.05);border-radius:10px;padding:17px;margin-top:15px}.addListPopup .popupInput{width:100%}.addListPopup .popupInput input{width:100%}.addListPopup .selectColor{display:flex;align-items:center;justify-content:center;width:100%;margin-top:13px}.addListPopup .selectColor .circle{width:20px;height:20px;cursor:pointer;border:2px solid transparent}.addListPopup .selectColor .circle__active{border:2px solid #525252}.addListPopup button{width:100%;padding:10px 62px;color:#fff;border-radius:4px;border:none;background-color:#42B883;margin-top:13px;cursor:pointer}.addListPopup button:hover{background-color:#379a6e}.addListPopup .krest{position:absolute;right:-8px;top:-8px;cursor:pointer}

.tasks__title{margin-left:5px;display:flex;align-items:center}.tasks__title svg{margin-left:10px;fill:#696969;opacity:0.5}.tasks__title h2{font-style:normal;font-weight:bold;font-size:32px;line-height:39px;color:black}.tasks__title:hover svg{opacity:0.8}.tasks__list{margin-top:45px}.tasks__list input{width:100%}.tasks__list .tasks__item{display:flex;margin-bottom:20px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap;align-items:center;justify-content:space-between}.tasks__list .tasks__item .tasks__item__svg{opacity:0;margin-left:5px}.tasks__list .tasks__item .taskItemWrap{display:flex;align-items:center;width:100%}.tasks__list .tasks__item .checkbox label{cursor:pointer;display:inline-flex;height:20px;width:20px;border-radius:50%;border:2px solid #ccb59b;margin-right:15px;align-items:center;justify-content:center}.tasks__list .tasks__item .checkbox label svg{opacity:0}.tasks__list .tasks__item .checkbox label:hover{background-color:#ccb59b;border-color:#ccb59b}.tasks__list .tasks__item .checkbox label:hover svg{opacity:1}.tasks__list .tasks__item .checkbox label:hover svg path{stroke:black}.tasks__list .tasks__item .checkbox input:checked+label{background-color:#4DD599;border:none}.tasks__list .tasks__item .checkbox input:checked+label svg{opacity:1}.tasks__list .tasks__item .checkbox input:checked+label svg path{stroke:#fff}.tasks__list .tasks__item .checkbox input{display:none}.tasks__list .tasks__item:hover .tasks__item__svg{opacity:1;stroke:black}.newTask{margin-top:25px;display:flex;align-items:center}.newTask button{display:flex;border:none;background-color:transparent;align-items:center;cursor:pointer;margin-bottom:50px}.newTask button:hover{opacity:0.8}.newTask button p{margin-left:20px;color:#B4B4B4;font-weight:500;font-size:16px;line-height:19px}.newTask .sendTaskBtn{display:flex}.newTask .sendTaskBtn .greenBtn{max-width:145px;padding:7px 5px 10px 5px;background-color:#4DD599;border:none;border-radius:4px;margin-right:9px;color:#fff}.newTask .sendTaskBtn .grayBtn{max-width:90px;padding:7px 5px 10px 5px;background-color:#DCDCDC;border:none;border-radius:4px;color:#9C9C9C}.newTaskInput{margin-top:20px}.changeTaskInput{padding:5px;background:#FFFFFF;border:1px solid #767676;box-sizing:border-box;border-radius:4px}.emptyTasks p{font-style:normal;font-weight:bold;font-size:32px;line-height:39px;text-align:center;color:#ccb59b}

